<template>
  <div class="navbar">
    <div class="navbar_box">
      <div class="logo"></div>
      <div class="navBtn" v-if="!isCellPhone">
        <ul>
          <li>Home</li>
          <li>History</li>
          <li>Stills x Film</li>
          <li>Character</li>
          <li>Staff</li>
        </ul>
      </div>
      <div class="navBtn_cp" v-if="isCellPhone" @click="showNavBox()">
        <i class="fas fa-bars fa-2x"></i>
      </div>
      <div class="navBox" id="navBox" v-if="isCellPhone">
          <div class="navBox_close" @click="showNavBox()">
            <i class="fas fa-times fa-2x"></i>
          </div>
          <ul>
            <li><h2>Home</h2></li>
            <li><h2>History</h2></li>
            <li><h2>Stills x Film</h2></li>
            <li><h2>Character</h2></li>
            <li><h2>Staff</h2></li>
          </ul>
          <div class="navBox_pic"></div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      isCellPhone: false,
    };
  },
  methods: {
    checkScreen() {
      let w = screen.width;
      if (w <= 768) {
        this.isCellPhone = true;
      }
    },
    showNavBox(){
      let box = document.getElementById('navBox');
      if(box.classList[1]){
        box.classList.remove('showNavBox')
      }
      else{
        box.classList.add('showNavBox');
      }
    }
  },
  mounted() {
    this.checkScreen();
  },
};
</script>
