import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading: true
  },
  mutations: {
    loadingIsDone(state){
      state.loading = false;
    }
  },
  actions: {
    loadingIsDone (context){
      context.commit('loadingIsDone')
    }
  }
})
